import React, { Component } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import moment from 'moment';
import DataTable from "react-data-table-component";
import {isEmpty} from 'lodash';
import ThumbnailList from './ThumbnailList';

class PostList extends Component {
  constructor(props) {
    console.log('props--', props);
    super(props);
    this.state  = {
      password: '', //  
      rePassword: '', //  
      pMessage:' .', //   (    )
      show: false,
      blindStyle: {
        width:'100%'
      },
      bodyEle: '',
      loading: true,
      dataList: [],
      groupType: '',
      columns: [],
      writeButton: '',
      isLoading: true,
      contentEl: '',

    };  //  state .    false .
  }
  componentDidMount() {
    this.setState({
      groupType: this.props.groupType,
      columns: this._getDataColumns()
    });

    this._getCateroeyList();
    this._setWriteButton();
    // this._getList();
  }
  componentDidUpdate() {
    if(this.state.groupType !== this.props.groupType) {
      this.setState({
        groupType: this.props.groupType,
        // columns: this._getDataColumns()
      });
      this._setWriteButton();
      this._getCateroeyList();
    }
    console.log('componentDidUpdate--', this.props);

  }
  _setWriteButton() {
    if(isEmpty(this.props.userInfo)) {
      return;
    }
    let btnEl = ''
    if (this.props.groupType !== 'GEUST' && this.props.userInfo.grade === 'MASTER') {
      btnEl =  <button type="button" className="btn btn-dark btn-sm btn-fr">
              <Link to={`${this.props.pathInfo.url}?groupType=${this.props.groupType}#write`} className="text-white" style={{textDecoration: 'none'}}></Link>
              </button>
    } else if (this.props.groupType === 'GEUST') {
      btnEl =  <button type="button" className="btn btn-dark btn-sm btn-fr">
              <Link to={`${this.props.pathInfo.url}?groupType=${this.props.groupType}#write`} className="text-white" style={{textDecoration: 'none'}}></Link>
              </button>
    }
    this.setState({
      writeButton: btnEl,
    })
  }
  _getDataColumns() {
    return [
      {
        name: "",
        selector: row => `${row.name}`,
        sortable: true,
        grow: 1,
      },
      {
        name: " ",
        selector: row => row.subject,
        sortable: true,
        compact: false,
        grow: 8,
        cell: d => <Link to={{pathname:`${this.props.pathInfo.url}/detail`, search: `?id=${d.id}&groupType=${this.props.groupType}`}} className="nav-link text-dark">
        <td>{d.subject}</td>
      </Link>
      },
      {
        name: "",
        selector: row => row.created_at,
        sortable: true,
        right: false,
        grow: 1,
        format: d => moment(d.created_at).format('YYYY.MM.DD'),
        // cell: d => <span>{moment(d.created_at).format('YYYY.MM.DD')}</span>,
      }
    ]
  }
  
  _getList = async() => {
    const res = await axios.get('/api/posts/list');
    console.log('_getList--', res)
    const bodyEle = this.boardList(res.data.result);
    this.setState({
      bodyEle: bodyEle,
      loading: false,
      dataList: res.data.result,
      // password: '',
    }) //       
  // this.setState({ hello : res.data.hello })
  }
  _getCateroeyList = async() => {
    const res = await axios.get(`/api/posts/category-list?groupType=${this.props.groupType}`);
    console.log('_getList--', res)
    // const bodyEle = this.boardList(res.data.result);
    this.setState({
      // bodyEle: bodyEle,
      loading: false,
      dataList: res.data.result,
      isLoading: false,
      // password: '',
    }) //       
    this._setContents();
  } 
  _setContents() {
    let contentEl;
    if(this.props.groupType === 'GALLERY') {
      contentEl = <ThumbnailList />
    } else {
      contentEl = <DataTable
      columns={this.state.columns}
      data={this.state.dataList}
      responsive={true}
      // customTheme={this.darkTheme()}
      customStyles={this.customStyles()}
      defaultSortAsc={false}
      pagination
      paginationPerPage={15}
      highlightOnHover
      noDataComponent="  ."
      progressPending={this.state.isLoading}
    />
    }
    this.setState({
      contentEl: contentEl
    })
  }
  boardList(list) {
    try{
      const listItems = list.map((item, index) =>
      <tr key={index}>
        {/* <td>{item.id}</td> */}
        <td>{item.name}</td>
        {/* <Nav.Link href={`${this.props.pathInfo.url}/detail#${item.id}`}>
          <td>{item.subject}</td>
        </Nav.Link> */}
        <Link to={{pathname: `${this.props.pathInfo.url}/detail`, search: `?id=${item.id}&groupType=${this.props.location.state.groupType}`, state: {groupType: this.props.location.state.groupType}}}
        className="nav-link text-dark">
          <td>{item.subject}</td>
        </Link>
        <td>{this.dateTimeFormat(item.created_at)}</td>
      </tr>
      );
      return (
        <tbody>{listItems}</tbody>
      );

    } catch(e) {
      console.log('boardList err', e);
    }
  } 
  dateTimeFormat(date) {
    return moment(date).format('YYYY.MM.DD');
  }
  // goDetail() {
  //   return href="#home"
  // } 
  handleConfirmPassword = (e) => {
    this.setState({
      [e.target.name]: e.target.value
    })
    if (e.target.value !== '1234') {
      this.setState({
        pMessage: "  .",
        // password: '',
      }) //       
    } else {
      this.setState({
        blindStyle: {
          width:'0%'
        }
        // password: '',
      }) //       

    }
    console.log('password--', this.state.password);
  }
  customStyles = () => {
    return {
      rows: {
        style: {
          minHeight: '35px', // override the row height
        }
      },
      headCells: {
        style: {
          minHeight: '40px', // override the row height
          paddingLeft: '8px', // override the cell padding for head cells
          paddingRight: '8px',
          fontSize: '13px',
          fontWeight: 'bold',
          lineHeight: '30px'
        },
      },
      cells: {
        style: {
          paddingLeft: '3px', // override the cell padding for data cells
          paddingRight: '3px',
        },
      },
    };
  }
  darkTheme = () => {
    return {
      title: {
        fontSize: '22px',
        fontColor: '#FFFFFF',
        backgroundColor: '#363640',
      },
      contextMenu: {
        backgroundColor: '#E91E63',
        fontColor: '#FFFFFF',
      },
      header: {
        fontSize: '12px',
        fontColor: '#FFFFFF',
        backgroundColor: '#363640',
      },
      rows: {
        fontColor: '#FFFFFF',
        backgroundColor: '#363640',
        borderColor: 'rgba(255, 255, 255, .12)',
        hoverFontColor: 'black',
        hoverBackgroundColor: 'rgba(0, 0, 0, .24)',
      },
      cells: {
        cellPadding: '5px',
      },
    }

  }
  render() {
    return (
      <div>
        {/* <Route path={`${this.props.pathInfo.url}/:type`} component={BoardDetail}/> */}
        <div className="clearfix mb-2">
          {this.state.writeButton}
          {/* <button type="button" className="btn btn-dark btn-sm btn-fr">
            <Link to={`${this.props.pathInfo.url}?groupType=${this.state.groupType}#write`} className="text-white" style={{textDecoration: 'none'}}></Link>
          </button> */}
        </div>
        <div className="card">
          {this.state.contentEl}
          {/* <Table striped bordered hover size="sm">
            <colgroup>
              <col width="20%"></col>
              <col width="65%"></col>
              <col width="15%"></col>
            </colgroup>
            <thead>
              <tr>
                <th></th>
                <th> </th>
                <th></th>
              </tr>
            </thead>
            {this.state.bodyEle}
          </Table> */}
        </div>
      
      </div>
    );
  }
};

export default PostList;